import data from './data';


class ProgressController {
  constructor() {
    
	this.$onInit = AmCharts.makeChart( "progress", {
  "type": "serial",
  "theme": "light",
  "dataDateFormat": "YYYY-MM-DD",
  "graphs": [ {
    "id": "g1",
    "bullet": "round",
    "bulletBorderAlpha": 1,
    "bulletColor": "#FFFFFF",
    "bulletSize": 5,
    "hideBulletsCount": 50,
    "lineThickness": 2,
    "title": "red line",
    "useLineColorForBulletBorder": true,
    "valueField": "value"
  } ],
  "chartScrollbar": {
    "graph": "g1",
    "oppositeAxis": false,
    "offset": 30,
    "scrollbarHeight": 80,
    "backgroundAlpha": 0,
    "selectedBackgroundAlpha": 0.1,
    "selectedBackgroundColor": "#888888",
    "graphFillAlpha": 0,
    "graphLineAlpha": 0.5,
    "selectedGraphFillAlpha": 0,
    "selectedGraphLineAlpha": 1,
    "autoGridCount": true,
    "color": "#AAAAAA"
  },
  "chartCursor": {
    "cursorAlpha": 1,
    "cursorColor": "#258cbb"
  },
  "categoryField": "date",
  "categoryAxis": {
    "parseDates": true,
    "equalSpacing": true,
    "gridPosition": "middle",
    "dashLength": 1,
    "minorGridEnabled": true
  },
  "zoomOutOnDataUpdate": false,
  "listeners": [ {
    "event": "init",
    "method": (e) => {

      e.chart.zoomToIndexes( e.chart.dataProvider.length - 40, e.chart.dataProvider.length - 1 );
 
    }
  }, {
    "event": "changed",
    "method":( e ) => {
      
      e.chart.lastCursorPosition = e.index;
    }
  } ],
  "dataProvider": data
} );
}
}

export default ProgressController;



