//import graphssalo from './graphs';
import dataProvidersalo from './data';



class ProfitsController {
  constructor() {
    
    this.chart = AmCharts.makeChart( "profits", {
  "type": "serial",
  "theme": "light",
  "categoryField": "year",
  "rotate": true,
  "startDuration": 1,
  "categoryAxis": {
    "gridPosition": "start",
    "position": "left"
  },
  "columnSpacing": 0,
  "columnWidth": 0.6,
  "graphs": [ {
    /**
     * These two graphs are just here for labels
     * They're basically invisible, save for the label itself,
     * but underneath they're just stacked column graphs
     */
    "labelText": "[[title]]",
    "labelPosition": "inside",
    "fontSize": 14,
    "fillAlphas": 0,
    "lineAlpha": 0,
    "title": "Income",
    "type": "column",
    "valueField": "income",
    "showBalloon": false,
    "visibleInLegend": false
  }, {
    "labelText": "[[title]]",
    "labelPosition": "inside",
    "fontSize": 14,
    "fillAlphas": 0,
    "lineAlpha": 0,
    "title": "Expenses",
    "type": "column",
    "valueField": "expenses",
    "showBalloon": false,
    "visibleInLegend": false
  }, {
    /**
     * We start a new stack with this graph
     */
    "newStack": true,
    "balloonText": "Income:[[value]]",
    "labelText": "[[value]]",
    "labelPosition": "inside",
    "color": "#fff",
    "fillAlphas": 0.8,
    "lineAlpha": 0.2,
    "title": "Income",
    "type": "column",
    "valueField": "income"
  }, {
    "balloonText": "Expenses:[[value]]",
    "labelText": "[[value]]",
    "labelPosition": "inside",
    "color": "#fff",
    "fillAlphas": 0.8,
    "lineAlpha": 0.2,
    "title": "Expenses",
    "type": "column",
    "valueField": "expenses"
  } ],
  "valueAxes": [ {
    "stackType": "regular",
    "position": "top",
    "axisAlpha": 0
  } ],
  "dataProvider": dataProvidersalo
} );	  

}
}

export default ProfitsController;



